import {
  beforeEach,
  beforeEachProviders,
  describe,
  expect,
  it,
  inject,
} from '@angular/core/testing';
import { ComponentFixture, TestComponentBuilder } from '@angular/compiler/testing';
import { Component } from '@angular/core';
import { By } from '@angular/platform-browser';
import { ActivitiesNotificationComponent } from './activities-notification.component';

describe('Component: ActivitiesNotification', () => {
  let builder: TestComponentBuilder;

  beforeEachProviders(() => [ActivitiesNotificationComponent]);
  beforeEach(inject([TestComponentBuilder], function (tcb: TestComponentBuilder) {
    builder = tcb;
  }));

  it('should inject the component', inject([ActivitiesNotificationComponent],
      (component: ActivitiesNotificationComponent) => {
    expect(component).toBeTruthy();
  }));

  it('should create the component', inject([], () => {
    return builder.createAsync(ActivitiesNotificationComponentTestController)
      .then((fixture: ComponentFixture<any>) => {
        let query = fixture.debugElement.query(By.directive(ActivitiesNotificationComponent));
        expect(query).toBeTruthy();
        expect(query.componentInstance).toBeTruthy();
      });
  }));
});

@Component({
  selector: 'test',
  template: `
    <sa-activities-notification></sa-activities-notification>
  `,
  directives: [ActivitiesNotificationComponent]
})
class ActivitiesNotificationComponentTestController {
}

